.messageSender {
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  width: 100%;
}

.messageSender__top {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding: 15px;
}
.messageSender__top form {
  flex: 1;
  display: flex;
}
.messageSender__top form input {
   outline-width: 0;
   border: none;
   padding: 5px 20px;
   margin: 0 10px;
   border-radius: 999px;
   background-color: #eff2f5;
}
.messageSender__fileSelector{
    width: 20%;
}
.messageSender__top form button{
    height: 0;
    width: 0;
    background-color: transparent;
    color: transparent;
    border: 0;
}
.messageSender__input{
    flex: 1;
}

.messageSender__bottom{
    display: flex;
    justify-content:space-evenly;
}
.messageSender__option{
    display: flex;
    margin: 5px;
    padding: 20px;
    align-items: center;
    color: grey;
}
.messageSender__option h3{
    font-size: medium;
    margin-left: 10px;
}
.messageSender__option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}